#menu_box {
    margin-top: 2%;
    letter-spacing: 0.2rem;
    font-size: 2rem;
}

#menu_box>ul {
    display: flex;
    justify-content: center;
}

#menu_box li {
    width: 33%;
    text-align: center;
    transition: transform 0.3s ease;
}

#menu_box li a {
    display: inline-block;
    border-radius: 10px;
    padding: 6px 10px;
}



.curLocation {
    transform: scale(1.3);
    font-weight: bold;
}

#menu_box ul li.curLocation:after {
    content: '';
    position: absolute;
    bottom: 0;
    left: 50%;
    width: 50%;
    height: 1.5px;
    background-color: rgb(255, 255, 255);
    transform: translateX(-50%);
}