@media (max-width: 900px) {

    #menu_box {
        margin-top: 2%;
        letter-spacing: 0.2rem;
        font-size: 1.5rem !important;
    }

    #main_container {
        padding-left: 0 !important;
        padding-right: 0 !important;
    }

    .gallery {
        grid-template-columns: repeat(1, 1fr) !important;
    }

    .modal-body {
        flex-direction: column !important;
        align-items: center !important;
        overflow: auto !important;
        padding: 0 !important;
    }

    .modal-body>div:nth-child(1) {
        padding: 0 !important;
    }

    .modal-body img {
        width: 100% !important;
    }

    .close {
        font-size: 3rem !important;
        top: -0.4rem !important;
        right: 3% !important;
    }

    .modal-content {
        height: 70% !important;
        background-position: calc(100% + 3rem) 100% !important;
    }

    .modal-image-container {
        width: 100% !important;
        /* 이미지 컨테이너 너비 100% */
        padding-left: 0 !important;
        /* 패딩 제거 */
    }

    .modal-description {
        width: 100% !important;
        /* 설명 영역 너비 100% */
        padding-left: 0 !important;
        /* 패딩 제거 */
        text-align: center !important;
        /* 텍스트 중앙 정렬 */
    }

    .more-detail-container {
        text-align: center !important;
        padding-right: 0 !important;
    }

    .modal-body::-webkit-scrollbar-thumb {
        background-color: rgba(0, 0, 0, 0.5) !important;
        border-radius: 10px !important;
    }

    .modal-body::-webkit-scrollbar-track {
        background: rgba(0, 0, 0, 0.1) !important;
        border-radius: 10px !important;
    }

    .product-grid {
        grid-template-columns: 1fr !important;
    }

    .modal-description>p:nth-child(1) {
        font-size: 3rem !important;
    }

    .modal-description>p:nth-child(5) {
        margin-top: 0 !important;
        font-size: 1.5rem !important;
        padding-top: 3rem;
    }

    .modal-description>p:nth-child(2),
    .modal-description>p:nth-child(3),
    .modal-description>p:nth-child(4) {
        line-height: 1 !important;
        font-size: 1.5rem;
    }

    .moreDetail {
        color: black
    }


    .filter-select {
        width: 40% !important;
    }

    .search-input {
        width: 60% !important;
        padding: 10px 15px;
        font-size: 16px;
        border: 2px solid #4CAF50;
        border-radius: 5px;
        background-color: #f9f9f9;
        color: #333;
    }

    .card-image:hover {
        transform: none !important;
    }

    .contact-item {
        padding-bottom: 1rem !important;
    }


    .contact-item p {
        text-indent: 0 !important;
        font-size: 1.5rem;
        letter-spacing: 2px;
        font-weight: 500;
    }

    .contact-item h2 {
        text-indent: 0 !important;
        font-size: 2.5rem;
    }

    .contact-info h1 {
        display: none !important;
    }

    .contact-container {
        display: flex;
        flex-direction: column;
    }

    .contact-info {
        flex: 1;
    }

    .contact-info h1 {
        font-size: 4.5rem;
        letter-spacing: 2px;
    }

    .business-card {
        flex: 1 !important;
        /* 오른쪽 영역 */
        display: flex;
        flex-direction: column;
        align-items: center;
        order: -1 !important;
    }

    .card-image {
        margin-bottom: 0.5rem !important;
    }
}