.contact-container {
    display: flex;
    /* 좌우로 나누기 위해 flex 사용 */
    justify-content: space-between;
    /* 양쪽으로 공간 배분 */
    padding: 40px;
    background-color: #f5f5f5cf;
    color: black;
    border-radius: 10px;
    box-shadow: 0 4px 15px rgba(0, 0, 0, 0.1);
    margin: 20px auto;
    max-width: 1200px;
    /* 최대 너비 조정 */
}

.contact-info {
    flex: 1;
}

.contact-info h1 {
    font-size: 4.5rem;
    letter-spacing: 2px;
}

.business-card {
    flex: 1;
    /* 오른쪽 영역 */
    display: flex;
    flex-direction: column;
    align-items: center;
}

.card-image {
    width: 100%;
    /* 가로폭을 부모 요소에 맞춤 */
    max-width: 300px;
    /* 최대 너비 조정 */
    margin: 30px 0;
    /* 위아래 간격 */
    border-radius: 10px;
    /* 모서리 둥글게 */
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
    /* 그림자 효과 */
}

.card-image:hover {
    transform: scale(1.4);
}

.contact-item {
    padding-bottom: 1.4rem;
}


.contact-item p {
    text-indent: 4.5rem;
    font-size: 1.5rem;
    letter-spacing: 2px;
    font-weight: 500;
}

.contact-item h2 {
    text-indent: 2rem;
    font-size: 2.5rem;
}