.App {
  font-family: "Noto Sans KR", sans-serif;
  height: 100%;
  width: 80%;
  margin: auto;
  font-family: "Dongle", sans-serif;
  font-size: larger;
}

html {
  background-color: black;
  color: white;
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
  word-break: keep-all;
  font-weight: bold;
}


a {
  color: white;
  text-decoration: none;
}

li {
  list-style: none;
}

#main_container {
  padding: 50px 100px;
}

.detail {
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-image: linear-gradient(43deg, #a162e8, #f7ce68 46%, #85ffbd);
}

.small_txt {
  font-size: 2.5rem;
  letter-spacing: 0.5rem;
  margin-right: 1rem;
  margin-left: 0.3rem;
}

.dv_hr {
  margin-top: 15rem;
  margin-bottom: 10rem;
}

.icon {
  font-size:1rem;
  margin-left: 0.5rem;
  color: black;
}

.small {
  font-size: 1.2rem;
  margin-right: 2%;
}