#cre {
    padding: 20px;
}

.filter-container {
    display: flex;
    justify-content: center;
    align-items: center;
    margin-bottom: 20px;
}

.filter-select {
    width: 10%;
    margin-right: 10px;
    padding: 10px 15px;
    font-size: 16px;
    border: 2px solid #4CAF50;
    border-radius: 5px;
    background-color: #f9f9f9;
    color: #333;
    appearance: none;
    cursor: pointer;
}

.search-input {
    width: 30%;
    padding: 10px 15px;
    font-size: 16px;
    border: 2px solid #4CAF50;
    border-radius: 5px;
    background-color: #f9f9f9;
    color: #333;
}

.search-input:focus {
    outline: none;
    border-color: #4CAF50;
    box-shadow: 0 0 5px rgba(76, 175, 80, 0.5);
}

.product-grid {
    display: grid;
    grid-template-columns: repeat(4, 1fr);
    gap: 20px;
}

.product-card {
    border-radius: 8px;
    overflow: hidden;
    text-align: center;
    padding: 10px;
    background-color: #fff;
    height: 25rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}

.product-card:hover {
    cursor: pointer;
    transform: scale(1.05);
}

.product-image {
    width: 100%;
    height: 78%;
    object-fit: cover;
    padding-bottom: 0.3rem;
}

.product-name {
    font-size: 2rem;
    flex-shrink: 0;
    color: black !important;
}

.product-info {
    font-size: 1rem;
    color: #666;
    flex-shrink: 0;
    font-weight: 500;
}

.cre_detail {
    text-align: center;
    font-size: 7rem;
}

.cre_detailTop {
    /* margin-top: 0 !important; */
    letter-spacing: 0.5rem;
}

