.modal-overlay {
    position: fixed;
    top: -3rem;
    left: 0;
    right: 0;
    bottom: 0;
    background-color: rgba(0, 0, 0, 0.7);
    display: flex;
    align-items: center;
    justify-content: center;
    z-index: 100;
}

.modal-content {
    position: relative;
    max-width: 600px;
    max-height: 80%;
    width: 80%;
    height: auto;
}

.modal-content img {
    width: 80%;
    height: auto;
    filter: brightness(1.05);
}

.close {
    position: absolute;
    top: -1rem;
    right: 2rem;
    font-size: 7rem;
    color: black;
    cursor: pointer;
    transition: transform 0.3s ease;
    z-index: 101;
}

.close:hover {
    transform: scale(1.3)
}

.modal-body {
    display: flex;
    align-items: center;
    justify-content: center;
    overflow-y: auto;
}

.modal-body img {
    width: 40%;
    height: auto;
}

.modal-overlay .modal-description {
    padding-left: 5rem;
    padding-right: 20px;
    width: 50%;

}

.modal-description>p:nth-child(1) {
    font-size: 6rem;
}

.modal-description>p:nth-child(5) {
    margin-top: 1rem;
}

.modal-description>p:nth-child(2),
.modal-description>p:nth-child(3),
.modal-description>p:nth-child(4) {
    line-height: 3;
}


.moreDetail {
    position: absolute;
    bottom: 1.2rem;
    right: 1.7rem;
    color: black;
    transition: transform 0.3s ease;
}

.moreDetail:hover {
    transform: scale(1.3);
    background-color: black;
    padding: 3px 10px;
    color: white;
    border-radius: 1rem;
}

.moreDetail:hover .icon {
    color: white;
}